import React, { useState, useEffect } from 'react';
import './home.css';
import { FiEdit } from 'react-icons/fi';
import  {AiOutlineDelete} from 'react-icons/ai'

export default function Home() {

    const [grocery, setGrocery] = useState('');
    const [displayGrocery, setDisplayGrocery] = useState([]);
    const [error, setError] = useState(false);
    const [activeInput, setActiveInput] = useState(true);
    const [editInput, setEditInput] = useState(false);
    const [editId, setEditId] = useState('');
    const [listAddedText, setListAddedText] = useState(false);
    const [editedText, setEditedText] = useState(false);
    const [deletedList, setDeletedList] = useState(false);
    const [emptyList, setEmptyList] = useState(false);
   const text = 'Item changed!';
   const deletedText = 'Item removed';
   const clearText = 'List is empty';


    const handleinputBox = (defaultEvent) =>{
            const value = defaultEvent.target.value;
            setGrocery(value);
    };

    const handleSubmit = (eventhandler) =>{
        if(grocery){
              eventhandler.preventDefault();
        const list = {id: new Date().getTime().toString(), grocery}
        setDisplayGrocery([...displayGrocery, list]);
        setListAddedText(true)
        setEditedText(false)
        
        }
        else{

        }
        setGrocery('') 
       /*setDisplayGrocery((displayGrocery)=>{
           return [...displayGrocery, list]
       })*/
    };

    const handleDelete = (deletedId) =>{
        let deletedItem = displayGrocery.filter((singleDeletedItem) => singleDeletedItem.id !== deletedId)
        setDisplayGrocery(deletedItem);
        setDeletedList(true);
        setEmptyList(false)

    };

    const handleEdit = (editId) =>{
        let editedItem = displayGrocery.filter((singleEditedId) => singleEditedId.id === editId);
        editedItem.map((singleEditedItem) =>{
            const {id, grocery} = singleEditedItem;
            setActiveInput(false);
            setEditInput(true)
            setGrocery(grocery);
            setEditId(id)
            
        })
    };
    
    const handleEditInput = ()=>{
        setDisplayGrocery(
            displayGrocery.map((singleUpdatedEdit)=>{
                if(singleUpdatedEdit.id === editId){
                    setActiveInput(true);
                    setEditInput(false);
                    setListAddedText(true)
                    setEditedText(true)
                    return {...singleUpdatedEdit, grocery};
                }
                setGrocery('')
                return singleUpdatedEdit;
    })
        )
   
    }
    const HandleCLear = ()=>{
        setDisplayGrocery([]);
        setDeletedList(true);
        setEmptyList(true)
    }
    
    useEffect  (() => {
        let addedTimer = setTimeout(() => {
            setListAddedText(false)
             setDeletedList(false)
        }, 2000);
        return () => {
            clearTimeout(addedTimer);
        }
    }, [listAddedText, deletedList]);

    const handleFocus = ()=>{
        
    }

    return (
        <div className='container'>
            <section className='wrapper'>
                <div className={listAddedText ? 'addedList': 'addedList1'} ><h4 className="addedTexth4" >{editedText ? text:  'Item added successfully' }</h4></div>

                <div className={deletedList? 'addedList': 'addedList1'}><h4 className="addedTexth4" >{emptyList? clearText : deletedText}</h4></div>
        
                <h3 className="pageTitle">Grocery Bud</h3>
                <form onSubmit={handleSubmit}>
                   
        
                    <input className='inputBox' type="text" placeholder='e.g Vegetables' 
                        value={grocery} id='inputcontainer' name='inputcontainer '
                        maxLength="40"
                        onChange={handleinputBox}
                        onFocus={handleFocus}
                    /> 
                   
                    <button className={`${activeInput ? 'btn': 'hideInput'}`} type='submit' >Submit</button>
                    <button className={`${editInput ? 'btn': 'hideInput'}`} type='button' onClick={handleEditInput} >EditItem </button>

                </form>
                <div className="displayContainer">
                    {displayGrocery.map((singleList)=>{
                        const {id, grocery} = singleList
                         return (
                         <>
                         <div className='displayList' key={id}>
                             
                            <div className='textContainer'>
                           <h4 className='displayText'>{grocery}</h4></div>
                            
                            <div className='iconContainer'>
                            <FiEdit className="edit" onClick={() => handleEdit(id)}/>
                            <AiOutlineDelete className='delete'onClick={()=> handleDelete(id)}/>
                            </div>
                       
                        </div>
                        
                     
                         </>
                         )
                        })}
                          
                </div>
                <button className="btn btnClear" onClick={HandleCLear}>Clear List</button>
            </section>
            
        </div>
    )
}
